<template lang='pug'>
  b-container
    section.admin
      h1.title –ü–∞–Ω–µ–ª—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞ {{ group.name }}
      b-tabs.mt-3
        b-tab(title='–°–æ–∑–¥–∞—Ç—å —Ä–æ–ª—å –≤ –≥—Ä—É–ø–ø–µ')
          b-row
            b-col
              b-form(@submit.prevent='createUser' class='mb-3 mt-3')

                b-form-group(label='–§–ò–û –≤–ª–∞–¥–µ–ª—å—Ü–∞')
                  b-form-input(
                    type='text'
                    v-model='user.name'
                    placeholder='–§–∞–º–∏–ª–∏—è –ò–º—è –û—Ç—á–µ—Å—Ç–≤–æ'
                    required)

                b-form-group(label='–ù–∞–∑–≤–∞–Ω–∏–µ —Ä–æ–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è')
                  b-form-input(
                    type='text'
                    v-model='user.role'
                    required
                    placeholder='–†–æ–ª—å')

                b-form-group(label='–°—Ä–æ–∫ –¥–µ–π—Å—Ç–≤–∏—è —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç–∞, —Å—É—Ç–æ–∫')
                  b-form-input(
                    type='number'
                    v-model='user.cerTime'
                    placeholder='0 - –Ω–µ–æ–≥—Ä–∞–Ω–∏—á–µ–Ω–æ'
                    required)

                b-form-group(
                  label='–í–≤–µ–¥–∏—Ç–µ email –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è'
                  description='–ù–∞ –Ω–µ–≥–æ –æ—Ç–ø—Ä–∞–≤–∏—Ç—Å—è —É–≤–µ–¥–æ–º–ª–µ–Ω–∏–µ –∏ –∫–ª—é—á–∏ –ø–æ–¥–ø–∏—Å–∏!')
                  b-form-input(
                    type='email'
                    v-model='user.email'
                    required
                    placeholder='example@mail.ua')

                b-button(type='submit') –°–æ–∑–¥–∞—Ç—å
            b-col.mt-3
                h2.subtitle –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–∏ –≥—Ä—É–ø–ø—ã {{ group.name }}
                b-list-group.users-list
                  b-list-group-item(v-for='—ÅurrUser in users' :key='—ÅurrUser._id')
                    h4 {{ —ÅurrUser.author }}
                    p –†–æ–ª—å: {{ —ÅurrUser.role }}
                    p(v-if='—ÅurrUser.dateRegistration') –ó–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω: {{ toDateString(+—ÅurrUser.dateRegistration) }}
        b-tab(title='–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –≥—Ä—É–ø–ø—ã')
          users-stat()

    b-modal(ref='infoModal' title='–°–æ–æ–±—â–µ–Ω–∏–µ')
      | {{ info }}
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      user: {
        role: '',
        email: '',
        login: '',
        name: '',
        certTime: 0
      },
      info: ''
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['groups']),
    ...mapGetters('usersStore', ['currentUser']),
    users () {
      return this.groups[0].users
    },
    group () {
      return this.groups[0]
    }
  },
  methods: {
    ...mapActions('groupsStore', ['createNewUser', 'getCurrentGroup']),
    ...mapActions('usersStore', ['createKeys']),
    toDateString,
    createUser (e) {
      this.createNewUser({ ...this.user, group: this.group._id })
        .then(response => {
          e.target.reset()
          this.info = response.message
          this.$refs.infoModal.show()
        })
        .catch(e => {
          this.info = e.message
          this.$refs.infoModal.show()
        })
    }
  },
  components: {
    usersStat: require('./usersStat')
  }
}
</script>
<style lang='sass' scoped>
.admin
  padding: 40px 0
</style>



