<template lang='pug'>
b-tabs
  b-tab(title='–ö–∞–∫ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å')
    b-form(@submit.prevent='signIn').mt-3
      b-alert(varinat='success' v-if='success' show) –í—ã —É—Å–ø–µ—à–Ω–æ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–ª–∏—Å—å!
      b-alert(variant='danger' v-if='error' show) {{ error }}
      b-form-group(label='–í–∞—à –ª–æ–≥–∏–Ω EDMS')
        b-form-input(
          v-model='userLogin'
          placeholder='–õ–æ–≥–∏–Ω'
          required)
      b-form-group(label='–ó–∞–≥—Ä—É–∑–∏—Ç–µ –ø—Ä–∏–≤–∞—Ç–Ω—ã–π –∫–ª—é—á')
        b-form-file(
          required
          v-model='privateKeyFile'
          accept='.key'
          placeholder='–§–∞–π–ª –ø—Ä–∏–≤–∞—Ç–Ω–æ–≥–æ –∫–ª—é—á–∞')
      b-form-group(label='–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å–Ω—É—é —Ñ—Ä–∞–∑—É')
        b-form-input(
          v-model='passphrase'
          type='password'
          placeholder='–ü–∞—Ä–æ–ª—å–Ω–∞—è —Ñ—Ä–∞–∑–∞ –∫–ª—é—á–∞'
          required
          )
      b-button(type='submit') –ê–≤—Ç–æ—Ä–∏–∑–æ–≤–∞—Ç—å—Å—è
  b-tab(title='–ö–∞–∫ –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä')
    b-form(@submit.prevent='signInAdmin').mt-3
      b-alert(varinat='success' v-if='success' show) –í—ã —É—Å–ø–µ—à–Ω–æ –∞–≤—Ç–æ—Ä–∏–∑–æ–≤–∞–ª–∏—Å—å!
      b-alert(variant='danger' v-if='error' show) {{ error }}
      b-form-group(label='–í–≤–µ–¥–∏—Ç–µ –ª–æ–≥–∏–Ω')
        b-form-input(
          v-model='userLogin'
          type='text'
          placeholder='–í–∞—à –ª–æ–≥–∏–Ω'
          required
          )
      b-form-group(label='–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å')
        b-form-input(
          v-model='userPassword'
          type='password'
          placeholder='–í–∞—à –ø–∞—Ä–æ–ª—å'
          required
          )
      b-button(type='submit') –ê–≤—Ç–æ—Ä–∏–∑–æ–≤–∞—Ç—å—Å—è
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      userLogin: '',
      userPassword: '',
      passphrase: '',
      privateKeyFile: null,
      success: false,
      error: ''
    }
  },
  methods: {
    ...mapActions('usersStore', ['getCurrentUser']),
    ...mapActions(['logIn', 'logInAdmin']),
    signIn (e) {
      this.logIn({
        login: this.userLogin,
        passphrase: this.passphrase,
        privateKeyFile: this.privateKeyFile
      })
        .then(() => {
          this.success = true
          this.error = ''
          this.$router.push('edms')
        })
        .catch(err => {
          this.success = false
          this.error = err.message
        })
    },
    signInAdmin (e) {
      this.logInAdmin({
        userLogin: this.userLogin,
        userPassword: this.userPassword
      })
        .then(() => {
          this.success = true
          this.error = ''
          this.getCurrentUser()
            .then(() => {
              this.$router.push('edms/admin')
            })
        })
        .catch(err => {
          this.success = false
          this.error = err.message
        })
    }
  }
}
</script>



