<template lang='pug'>
  b-row
    b-col
      h2.subtitle.mb-2 –°–ø–∏—Å–æ–∫ –≥—Ä—É–ø–ø
          b-tabs(vertical card pills).users-list
            b-tab(v-for='group in groups' :key='group._id' :title='group.name + " –ü–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π: " + group.users.length')
              b-list-group
                b-list-group-item(
                  v-for='user in group.users'
                  :key='user._id'
                  )
                  h4 {{ user.author ? user.author : '–ù–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω' }}
                  p.small –†–æ–ª—å: {{ user.role ? user.role : '–ù–µ –∑–∞—Ä–µ–≥–∏—Å—Ç—Ä–∏—Ä–æ–≤–∞–Ω' }}
                  p.small –î–∞—Ç–∞ —Ä–µ–≥–∏—Å—Ç—Ä–∞—Ü–∏–∏: {{ toDateString(+user.dateRegistration) }}
                  p.small Email: {{ user.email }}
                  p.small Login: {{ user.login }}
                  b-button-group(v-if='user.role === "Admin"')
                    b-button(@click='openSendModal(user._id, user.email)' variant='secondary') –û—Ç–ø—Ä–∞–≤–∏—Ç—å –¥–æ—Å—Ç—É–ø –ø–æ–≤—Ç–æ—Ä–Ω–æ
                    b-button(@click='deleteAdmin(user._id, group._id)' variant='warning') –£–¥–∞–ª–∏—Ç—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞
    b-modal(
      ref='sendModal'
      title='–£–∫–∞–∂–∏—Ç–µ –ø–æ—á—Ç—É, –Ω–∞ –∫–æ—Ç–æ—Ä—É—é –æ—Ç–ø—Ä–∞–≤–∏—Ç—Å—è –¥–æ—Å—Ç—É–ø'
      @ok='submitSend')
      b-form-group(label='Email –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞')
        b-form-input(type='email' v-model='admin.email')
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../../modulesJs/toDateString'
export default {
  data () {
    return {
      admin: {
        email: '',
        adminId: ''
      }
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['groups'])
  },
  methods: {
    ...mapActions('groupsStore',
      [
        'getAllGroups',
        'removeAdmin',
        'sendInviteAdmin'
      ]),
    toDateString,
    deleteAdmin (adminId, groupId) {
      this.removeAdmin({ adminId, groupId })
    },
    openSendModal (adminId, adminEmail) {
      this.admin = {
        email: adminEmail,
        adminId
      }
      this.$refs.sendModal.show()
    },
    submitSend () {
      console.log(this.admin)
      this.sendInviteAdmin(this.admin)
        .then(response => { console.log(response); this.$emit('alert', response.message) })
        .catch(e => this.$emit('alert', e.message))
    }
  },
  created () {
    this.getAllGroups()
  }
}
</script>



