<template lang='pug'>
  div
    b-form-group(label='–í—ã–±—Ä–∞—Ç—å –ø—Ä–æ–º–µ–∂—É—Ç–æ–∫ —Å—Ä–µ–∑–∞ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏').mt-3
      b-button(type='button' @click='showCalendar = !showCalendar') –û—Ç–∫—Ä—ã—Ç—å –∫–∞–ª–µ–Ω–¥–∞—Ä—å
    h4 –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –∑–∞ {{ title }}
    p –°–æ–∑–¥–∞–Ω–æ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ –∑–∞ –ø–µ—Ä–∏–æ–¥: {{ docsCreated}}
    p –ü–æ–¥–ø–∏—Å–∞–Ω–æ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ –∑–∞ –ø–µ—Ä–∏–æ–¥: {{ docsSigned }}
    p –û—Ç–∫–∞–∑–∞–Ω–æ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ –∑–∞ –ø–µ—Ä–∏–æ–¥: {{ docsRejected }}
    p –°—Ä–µ–¥–Ω–µ–µ –≤—Ä–µ–º—è –º–µ–∂–¥—É —Å–æ–∑–¥–∞–Ω–∏–µ–º –∏ —Ñ–∏–Ω–∞–ª—å–Ω—ã–º –ø–æ–¥–ø–∏—Å–∞–Ω–∏–µ–º –¥–æ–∫—É–º–µ–Ω—Ç: {{ docsTimingResolve }}
    p –°—Ä–µ–¥–Ω–µ–µ –≤—Ä–µ–º—è –æ–¥–Ω–æ–π –ø–æ–¥–ø–∏—Å–∏: {{ docsTimingSign }}
    h4 –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –ø–æ —Ä–æ–ª—è–º:
    b-table(
      striped
      bordered
      hover
      :fields='usersStat.fields'
      :items='usersStat.items'
      )
    b-button(type='button' @click='submitStat') –ü–æ–ª—É—á–∏—Ç—å —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫—É
    b-modal(v-model='showCalendar' title='–í—ã–±—Ä–∞—Ç—å –ø—Ä–æ–º–µ–∂—É—Ç–æ–∫ —Å—Ä–µ–∑–∞ —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏' hide-footer )
      date-range(
          class='calendar'
          :first-day-of-week='1'
          v-model='date'
          lang='ru')
</template>

<script>
import { DateRange } from 'vue-date-range'
import { mapGetters, mapActions, mapMutations } from 'vuex'
export default {
  data () {
    return {
      date: {
        startDate: null,
        endDate: null
      },
      loadedStat: false,
      showCalendar: false,
      title: '–≤—Å–µ –≤—Ä–µ–º—è'
    }
  },
  computed: {
    ...mapGetters('statStore', [
      'docsCreated',
      'docsSigned',
      'docsRejected',
      'docsTimingSign',
      'docsTimingResolve',
      'usersStat'
    ])
  },
  methods: {
    ...mapActions('statStore', ['getDocsStat']),
    ...mapMutations('statStore', ['setRange']),
    submitStat () {
      const start = Date.parse(this.date.startDate._d)
      const end = Date.parse(this.date.endDate._d)
      this.title = `–ø–µ—Ä–∏–æ–¥ —Å ${this.toDateString(start)} –ø–æ ${this.toDateString(
        end
      )}`
      this.setRange({ start, end })
      this.showCalendar = false
    }
  },
  created () {
    this.getDocsStat()
      .then(response => {
        this.loadedStat = true
      })
  },
  components: {
    DateRange
  }
}
</script>

<style lang='sass' scoped>
.users-list
  max-height: 445px
  overflow-y: scroll
  overflow-x: hidden
ol
  list-style: inside
</style>



