<template lang='pug'>
.bg-simple
  b-container
    b-row
      b-col(sm='12' class='mb-3')
        b-card
          h1.title {{ document.title }}
      b-col(sm='12' class='mb-3')
        b-card
          time.text –î–∞—Ç–∞ –ø–µ—Ä–≤–æ–π –ø—É–±–ª–∏–∫–∞—Ü–∏–∏ {{ toDateString(+document.date) }}
          p.text(v-if='document.versions.lenght > 1') –¢–µ–∫—É—â–∞—è –≤–µ—Ä—Å–∏—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ {{ document.versions[0].version }}
          time.text(v-if='document.versions.lenght > 1') –î–∞—Ç–∞ –ø—É–±–ª–∏–∫–∞—Ü–∏–∏ –≤–µ—Ä—Å–∏–∏ {{ document.versions[0].version }}: {{ document.versions[0].date }}
    b-row
      b-col
        doc-tabs
    b-row
      b-col
        signers-list(:rejected='false')
      // document info
      b-col(sm='12' lg='6')
        b-alert(
          variant='success'
          v-if='document.globalStatus === "resolved"'
          class='mb-0'
          show
          ) –î–æ–∫—É–º–µ–Ω—Ç —É—Å–ø–µ—à–Ω–æ –ø–æ–¥–ø–∏—Å–∞–Ω, –µ–≥–æ –º–æ–∂–Ω–æ –Ω–∞–π—Ç–∏ –≤
          router-link(:to='"/edms/archive/" + document._id') –ê—Ä—Ö–∏–≤–µ
        b-alert(
          variant='primary'
          v-if='document.globalStatus === "waiting"'
          class='mb-0'
          show
          ) –î–æ–∫—É–º–µ–Ω—Ç –Ω–∞ —Å—Ç–∞–¥–∏–∏ —Ä–∞—Å—Å–º–æ—Ç—Ä–µ–Ω–∏—è. –°–µ–π—á–∞—Å –µ–≥–æ —Ä–∞—Å—Å–º–∞—Ç—Ä–∏–≤–∞–µ—Ç: {{ closerViewer }}
        div(v-if='document.globalStatus === "rejected"')
          b-alert(
            variant='danger'
            show
            ) –î–æ–∫—É–º–µ–Ω—Ç –æ—Ç–∫–∞–∑–∞–Ω –≤ –ø–æ–¥–ø–∏—Å–∏. –í—ã –º–æ–∂–µ—Ç–µ –µ–≥–æ –¥–æ—Ä–∞–±–æ—Ç–∞—Ç—å –∏–ª–∏ –∑–∞–∫—Ä—ã—Ç—å.
            b-card.mb-3
              p.subtitle.subtitle_small {{ document.versions[0].rejectReason }}
          b-button(
            id='toArchive'
            @click='closeDocument'
            size='md'
            class='mr-3 mb-3'
            ) –ó–∞–∫—Ä—ã—Ç—å –¥–æ–∫—É–º–µ–Ω—Ç
          router-link(
            id='editDocument'
            :to='"/edms/myDocumentEdit/" + id'
            class='btn btn-success btn-md mb-3'
            ) –î–æ—Ä–∞–±–æ—Ç–∞—Ç—å –¥–æ–∫—É–º–µ–Ω—Ç
          b-tooltip(target='editDocument' title='–ü–µ—Ä–µ–π—Ç–∏ –≤ —Ä–µ–¥–∞–∫—Ç–æ—Ä –≤–µ—Ä—Å–∏–π')
          b-tooltip(target='toArchive' title='–î–æ–∫—É–º–µ–Ω—Ç –æ—Ç–ø—Ä–∞–≤–∏—Ç—Å—è –≤ –∞—Ä—Ö–∏–≤')
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      id: this.$router.currentRoute.params.id
    }
  },
  computed: {
    ...mapGetters('docsStore', ['document']),
    closerViewer () {
      return this.document.routes.find(route => route.status === 'waiting').author
    }
  },
  methods: {
    ...mapActions('docsStore', ['getMyDocumentById', 'closeDocumentById']),
    toDateString,
    closeDocument () {
      this.closeDocumentById(this.id)
    }
  },
  created () {
    this.getMyDocumentById(this.id)
      .catch(e => {
        this.$router.push('404')
      })
  },
  components: {
    docTabs: require('../_common/docTabs'),
    signersList: require('../_common/signersList')
  }
}
</script>



