<template lang='pug'>
  b-row
    b-col
      b-form(@submit.prevent='createAdmin' ref='createAdminForm')
          h2.subtitle –°–æ–∑–¥–∞—Ç—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–ø—ã
          b-form-group(
            label='–í—ã–±–µ—Ä–∏—Ç–µ –≥—Ä—É–ø–ø—É:'
            description='–ì—Ä—É–ø–ø–∞, –¥–ª—è –∫–æ—Ç–æ—Ä–æ–π —Å–æ–∑–¥–∞—Ç—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞')
            b-form-select(
              v-model='admin.groupId'
              :options='groupsOptions'
              class='mb-3')
          b-form-group(
            label='e-mail –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–ø—ã'
            description='–í–Ω–∏–º–∞–Ω–∏–µ! –ù–∞ —ç—Ç–æ—Ç –∞–¥—Ä–µ—Å –±—É–¥—É—Ç –≤—ã—Å–ª–∞–Ω—ã –¥–∞–Ω–Ω—ã–µ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏!')
            b-form-input(
              type='email'
              v-model='admin.email'
              placeholder='example@gmail.com'
              required)

          b-button(type='submit' variant='primary') –°–æ–∑–¥–∞—Ç—å –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import randomizer from '../../modulesJs/randomizer'
export default {
  data () {
    return {
      admin: {
        email: '',
        groupId: ''
      }
    }
  },
  methods: {
    ...mapActions('groupsStore', ['createNewAdmin']),
    generateInvite () {
      return randomizer(5)
    },
    createAdmin () {
      this.createNewAdmin({ ...this.admin })
        .then(response => {
          this.$refs.createAdminForm.reset()
          this.$emit('alert', response.message)
        })
        .catch(e => {
          this.$emit('alert', e.message)
        })
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['groups']),
    groupsOptions () {
      const resultOptions = [{ value: '', text: '–ù–µ –≤—ã–±—Ä–∞–Ω–æ' }]
      for (let group of this.groups) {
        resultOptions.push({ value: group._id, text: group.name })
      }
      return resultOptions
    }
  }
}
</script>



