<template lang='pug'>
  b-row
    b-col
      b-form(@submit.prevent='createGroup' ref='createGroupForm' class='mb-3')
        h2.subtitle.mb-2 –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤—É—é –≥—Ä—É–ø–ø—É
        b-form-group(label='–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã')
          b-form-input(
          type='text'
          v-model='groupName'
          required
          placeholder='–í–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã')
        b-form-group(
          label='e-mail –∞–¥–º–∏–Ω–∏—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–ø—ã'
          description='–í–Ω–∏–º–∞–Ω–∏–µ! –ù–∞ —ç—Ç–æ—Ç –∞–¥—Ä–µ—Å –±—É–¥—É—Ç –≤—ã—Å–ª–∞–Ω—ã –¥–∞–Ω–Ω—ã–µ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏!')
          b-form-input(
            type='email'
            v-model='adminEmail'
            placeholder='example@gmail.com'
            required)
        b-button(type='submit' variant='primary') –°–æ–∑–¥–∞—Ç—å –≥—Ä—É–ø–ø—É

    b-modal(
      ref='alertModal'
      title='–í–Ω–∏–º–∞–Ω–∏–µ!'
      @ok='submitForm')
      h3 –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –¥–∞–Ω–Ω—ã–µ!
      b-form-group(label='–ù–∞–∑–≤–∞–Ω–∏–µ –≥—Ä—É–ø–ø—ã')
        b-form-input(
          v-model='groupName'
          readonly)
      b-form-group(
        label='Email –∞–¥–º–∏–Ω—Å—Ç—Ä–∞—Ç–æ—Ä–∞'
        description='–ù–∞ —ç—Ç—É –ø–æ—á—Ç—É –±—É–¥—É—Ç –≤—ã—Å–ª–∞–Ω—ã –ª–æ–≥–∏–Ω –∏ –ø–∞—Ä–æ–ª—å')
        b-form-input(v-model='adminEmail')

</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      groupName: '',
      adminEmail: '',
      info: ''
    }
  },
  methods: {
    ...mapActions('groupsStore', ['createNewGroup']),
    createGroup () {
      this.$refs.alertModal.show()
    },
    submitForm () {
      this.createNewGroup({ groupName: this.groupName, adminEmail: this.adminEmail })
        .then(response => {
          this.$refs.createGroupForm.reset()
          this.$emit('alert', response.message)
        })
        .catch(e => this.$emit('alert', e.message))
    }
  }
}
</script>
<style lang="sass" scoped>
.users-list
  max-height: 500px
  overflow-y: scroll
</style>



