<template lang='pug'>
.bg-simple
  b-container
    b-row
      b-col(sm='12' class='mb-3')
        b-card
          h1.title {{ document.title }}
      b-col(sm='12' class='mb-3')
        b-card
          p.text –ê–≤—Ç–æ—Ä –ø—É–±–ª–∏–∫–∞—Ü–∏–∏ {{ document.author}}
          time.text –î–∞—Ç–∞ –ø–µ—Ä–≤–æ–π –ø—É–±–ª–∏–∫–∞—Ü–∏–∏ {{ toDateString(+document.date) }}
          div(v-if='document.versions.length > 1')
            p.text –¢–µ–∫—É—â–∞—è –≤–µ—Ä—Å–∏—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ {{ document.versions[0].version }}
            time.text –î–∞—Ç–∞ –ø—É–±–ª–∏–∫–∞—Ü–∏–∏ –≤–µ—Ä—Å–∏–∏ {{ document.versions[0].version }}: {{ toDateString(+document.versions[0].date) }}
    b-row
      b-col
        doc-tabs
    b-row
      b-col(sm='12' lg='6')
        signers-list(:rejected='rejected')
      b-col(sm='12' lg='6')
        // sign form
        b-form(@submit.prevent='submitDoc')
          b-card
            b-form-group(label='–í—ã–±–µ—Ä–∏—Ç–µ –¥–µ–π—Å—Ç–≤–∏–µ')
              b-form-radio-group(
                v-model='selected'
                name='radioSubComponent'
                :options='options'
                )
            b-form-group(label='–í–∞—à –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π')
              b-form-textarea(
                v-model='comment'
                placeholder='–û—Å—Ç–∞–≤—å—Ç–µ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–π'
                :rows='3'
                :max-rows='6'
                required
                )
            b-button(type='submit') –û—Ç–ø—Ä–∞–≤–∏—Ç—å

  b-modal(ref='alertModal' hide-footer) {{ infoAlert }}
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      // get id of current document
      id: this.$router.currentRoute.params.id,
      // current status of doc
      selected: '',
      options: [
        { value: 'resolve', text: '–ü–æ–¥–ø–∏—Å–∞—Ç—å –¥–æ–∫—É–º–µ–Ω—Ç' },
        { value: 'reject', text: '–û—Ç–∫–∞–∑–∞—Ç—å –≤ –ø–æ–¥–ø–∏—Å–∏' }
      ],
      comment: '',
      infoAlert: '',
      rejected: false
    }
  },
  computed: {
    ...mapGetters('docsStore', ['document']),
    ...mapGetters('usersStore', ['currentUser']),
    ...mapGetters(['privateKey'])
  },
  methods: {
    ...mapActions('docsStore', ['getDocumentById', 'postVote']),
    toDateString,
    submitDoc (e) {
      if (this.selected && this.comment) {
        // post our vote to server
        this.postVote({
          id: this.id,
          vote: this.selected,
          comment: this.comment,
          author: this.currentUser
        })
          .then(response => {
            if (this.selected === 'reject') {
              this.rejected = true
            }
            this.showAlert(response.message)
            e.target.reset()
            this.getDocumentById(this.id)
          })
          .catch(e => {
            this.showAlert(`–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞: ${e.message}`)
          })
      }
    },
    showAlert (title) {
      this.infoAlert = title
      this.$refs.alertModal.show()
    }
  },
  created () {
    this.getDocumentById(this.id)
      .catch(() => {
        this.$router.push('404')
      })
  },
  components: {
    docTabs: require('../_common/docTabs'),
    signersList: require('../_common/signersList')
  }
}
</script>



