<template lang="pug">
.bg-simple
  b-container
    b-row
      b-col
        b-card.mb-3
          h1.title {{ document.title }}
    b-row
      b-col
        b-alert(show v-if='document.globalStatus==="resolved"' variant="success") –î–æ–∫—É–º–µ–Ω—Ç —É—Å–ø–µ—à–Ω–æ –ø—Ä–∏–Ω—è—Ç
        b-alert(show v-else variant="warning") –î–æ–∫—É–º–µ–Ω—Ç –æ—Ç–∫–∞–∑–∞–Ω –≤ –ø–æ–¥–ø–∏—Å–∏ –∏ –ø–æ–º–µ—â–µ–Ω –≤ –∞—Ä—Ö–∏–≤
        doc-tabs
    b-row
      b-col(sm="12")
        signers-list(:rejected="false")
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      id: this.$router.currentRoute.params.id
    }
  },
  computed: {
    ...mapGetters('docsStore', ['document'])
  },
  methods: {
    ...mapActions('docsStore', ['getArchiveDocumentById']),
    toDateString
  },
  created () {
    this.getArchiveDocumentById(this.id)
      .catch(e => {
        this.$router.push('404')
      })
  },
  components: {
    docTabs: require('../_common/docTabs'),
    signersList: require('../_common/signersList')
  }
}
</script>



